<template>
  <div class="aboutUs">
    <!-- , -->
    <TopNavBlack></TopNavBlack>
    <!--  -->
    <div class="position"></div>
    <!--  -->
    <div class="aboutImg">
      <img src="../../assets/images/banner2.png">
      <div class="aboutImgBox">
        <div class="aboutChi"></div>
        <div class="aboutEng">LS INSTITUTE</div>
      </div>
    </div>
    <!--  -->
    <div v-if="nDocName" class="contentBox">
      <div id="reader"></div>
    </div>
    <div v-else class="contentBox">
      <div class="infoTitle">
        <router-link tag="span" class="editBtn" :to="{path:'/Blog'}"> > </router-link><span class="information">{{nTitle}}</span>
      </div>
      <div class="title">{{nTitle}}</div>
      <div class="date">{{dts}}</div>
      <div class="content">
        <pre>
          {{nContent}}
        </pre>
      </div>
    </div>
    <!--  -->
    <Bottom></Bottom>
  </div>
  

</template>

<script>
import TopNavBlack from '@/components/common/TopNavBlack'
import Bottom from '@/components/common/Bottom'
import store from '@/vuex/store';
import {mapState,mapMutations} from 'vuex';
import axios from 'axios';

export default {
  name: 'BlogDetail',
  store,
  data () {
    return {
      lfnid : 0,
      nTitle : '',
      nContent : '',
      nPicture : '',
      nDocName : '',
      baiduDocID : '',
      extDocType : '',
      dts: ''
    }
  },
  computed:{
    ...mapState[('username')]
  },
  methods: {
    init: function(){
    },
    // textarea
    textareaTo: function(str){
      var reg=new RegExp("<br>","g");
      var regSpace=new RegExp("&nbsp;","g");
      str = str.replace(reg,"\n");
      str = str.replace(regSpace," ");
      return str;
    },
    // lfnid : ID      int
    // nTitle :       String
    // nContent :          String
    // nPicture :      String
    // nDocName :      String
    // baiduDocID :  ID    String
    // extDocType :      String
    // 
    getInforDetail: function(lfnid){
      var that = this;
      axios.get(common.globalUrl + '/exp/QuerylfNewsDetail.do?lfnid='+lfnid)
      .then(function(response){
        // console.log(response.data);
        var data = response.data;
        that.nTitle = data.nTitle;
        that.nContent = that.textareaTo(data.nContent);
        that.nPicture = data.nPicture;
        that.nDocName = data.nDocName;
        that.baiduDocID = data.baiduDocID;
        that.extDocType = data.extDocType;
        that.dts = new Date(data.ts).Format('yyyy-MM-dd hh:mm');
        that.baiduDocID && that.initBaiduDoc(data.baiduDocID);
      })
      .catch(function(error){
        alert('!!')
      })
    },
    initBaiduDoc: function(bdid){
      // console.log(bdid);
      var docId = bdid.substring(0,bdid.indexOf('_'));
      // console.log(docId);
      var option = {
          docId: docId,
          token: 'TOKEN',
          host: 'BCEDOC',
          width: 1000, //
          zoom: false,              //
          zoomStepWidth:200,
          pn: 1,  //
          ready: function (handler) {  // , 
              handler.setFontSize(1);
              handler.setBackgroundColor('#000');
              handler.setFontColor('#fff');
          },
          flip: function (data) {    // , 
              // console.log(data.pn);
          },
          fontSize:'big',
          toolbarConf: {
            page: true, //
            pagenum: true, //
            full: false, //,
            copy: true, //
            position: 'center',//  toolbar(left/center)
          } //,
      };
      new Document('reader', option);
    }
  },
  mounted:function(){
    this.init();
    var lfnid = this.$route.query.lfnid;
    if(!lfnid) return;
    this.getInforDetail(lfnid);
  },
  components:{
    TopNavBlack,
    Bottom
  }
}
</script>

