  width: 1000px;
  height: auto;
  margin: 0 auto 20px;
  position: relative;
}
.infoTitle{
  width: 1000px;
  height: 50px;
  line-height: 50px;
  border-bottom: 1px solid #ccc;
  color: #333;
  font-size: 16px;
  margin: 0 auto 10px;
}
